<template>
	<div>
		<div class="title">热销推荐</div>
		<ul>
			<router-link 
				tag="li" 
				class="item border-bottom" 
				v-for='recom of recommendList' 
				:key='recom.id'
				:to="'/detail/'+recom.id"
				>
				<img class="item-img" :src="recom.imgUrl"/>
				<div class="item-info">
					<p class="item-title" v-text='recom.title'></p>
					<p class="item-desc" v-text='recom.desc'></p>
					<button class="item-button">查看详情</button>
				</div>
			</router-link>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'HomeRecommend',
	props: {
		recommendList: Array
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/mixin.styl'
@import '~styles/varibles.styl'
	.title
		margin-top: .2rem
		line-height: .8rem
		background: #eeeeee
		text-indent: .2rem
	.item
		display: flex
		height: 1.9rem 
		.item-img
			width: 1.7rem
			height: 1.7rem
			padding: .1rem
		.item-info
			flex: 1
			padding: .1rem
			min-width: 0
			.item-title
				line-height: .54rem
				font-size: .32rem
				ellipsis()
			.item-desc
				ellipsis()
				line-height: .4rem
				color: #cccccc
			.item-button
				line-height: .44rem
				margin-top: .16rem
				background: #ff9300
				padding: 0 .2rem
				border-radius: .06rem
				color: $whiteTextColor
</style>


