<template>
	<div class="container" @click="handleGallaryClick">
		<div class="wrapper">
		  <swiper :options="swiperOptions">
		    <swiper-slide v-for="(img,index) of imgs" :key="index">
		    	<img class="gallary-img" :src="img"/>
		    </swiper-slide> 
		    <div class="swiper-pagination"  slot="pagination"></div>
		  </swiper>		
		</div>		
	</div>
</template>

<script>
export default {
	name: 'CommonGallary',
	props: {
		imgs: {
			type: Array,
			default () {
				return []
			}
		}
	},
	data () {
		return {
			swiperOptions: {
				pagination: '.swiper-pagination',
				paginationType: 'fraction',
				observeParents: true,
				observer: true
			}
		}
	},
	methods: {
		handleGallaryClick () {
			this.$emit('close')
		}
	}
}
</script>

<style lang="stylus" scoped>
	.container >>> .swiper-container
		overflow: inherit
	.container
		z-index: 99
		display: flex
		flex-direction: column
		justify-content: center
		position: fixed
		top: 0
		left: 0
		right: 0
		bottom: 0
		background: #000000
		.wrapper
			width: 100%
			height: 0
			padding-bottom: 63%
			.gallary-img
				width: 100%	
			.swiper-pagination
				color: #ffffff
				bottom: -1rem	
</style>


