<template>
	<div>
		<div class="search">
			<input class="search-input" v-model="keyword" type="text" placeholder="输入城市名或拼音" />
		</div>
		<div class="search-content" ref="search" v-show="keyword">
			<ul>
				<li class="search-item border-bottom" v-for="item of list" :key="item.id" @click="handleCityClick(item.name)">{{item.name}}</li>
				<li class="search-item border-bottom" v-show="hasNoData">没有找到匹配数据</li>
			</ul>
		</div>
	</div>

	
</template>

<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default{
	name: 'CitySearch',
	props: {
		cities: Object
	},
	data () {
		return {
			keyword: '',
			list: [],
			timer: null
		}
	},
	computed: {
		hasNoData () {
			return !this.list.length
		}
	},
	methods: {
		handleCityClick (city) {
			// this.$store.commit('changeCity', city) // 可用dispatch调用actions 或者直接commit调用 mutations
			this.changeCity(city)
			this.$router.push('/')
		},
		...mapMutations(['changeCity']) // 映射到vuex的mutation的changeCity方法
	},	
	mounted () {
		this.scroll = new BScroll(this.$refs.search);
	},
	watch: {
		keyword () {
			if( this.timer ) {
				clearTimeout(this.timer)
			}
			if( this.keyword == '' ) {
				this.list = []
				return false
			}
			this.timer = setTimeout(() => {
				const result = []
				for( let i in this.cities ) {
					this.cities[i].forEach((value)=> {
						if( value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1 ) {
							result.push(value)
						}
					})
				}
				this.list = result;
			}, 100)
		}
	}
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
.search
	height: .72rem
	background: $bgColor
	padding: 0 .1rem
	.search-input
		box-sizing: border-box
		width: 100%
		height: .62rem
		padding: 0 .1rem
		line-height: .62rem
		text-align: center
		border-radius: .06rem
		color: #666666
.search-content
	overflow: hidden
	position: absolute
	top: 1.58rem
	left: 0
	right: 0
	bottom: 0
	background: #eee
	z-index: 1
	.search-item
		line-height: .62rem
		padding-left: .2rem
		background: #ffffff
		color: #666
</style>


